@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

article,
aside,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
  display: block;
}

body {
  margin: 0;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0 !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

a {
  color: inherit;
  text-decoration: none;
  background-color: transparent;
}

a:hover {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([class]) {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([class]):hover {
  color: inherit;
  text-decoration: none;
}

pre,
code,
kbd,
samp {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
  width: 100%;
  max-width: 100%;
}

svg {
  overflow: hidden;
  vertical-align: middle;
}

table {
  border-collapse: collapse;
}

caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #6c757d;
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
  text-align: -webkit-match-parent;
}

label {
  display: inline-block;
  margin-bottom: 0.5rem;
}

button {
  border-radius: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

[role=button] {
  cursor: pointer;
}

select {
  word-wrap: normal;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button:not(:disabled),
[type=button]:not(:disabled),
[type=reset]:not(:disabled),
[type=submit]:not(:disabled) {
  cursor: pointer;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type=radio],
input[type=checkbox] {
  box-sizing: border-box;
  padding: 0;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

@media (max-width: 1200px) {
  legend {
    font-size: calc(1.275rem + 0.3vw);
  }
}
progress {
  vertical-align: baseline;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
  cursor: pointer;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  margin-bottom: 0.5rem;
  font-weight: 500;
  line-height: 1.2;
}

h1,
.h1 {
  font-size: 2.5rem;
}

@media (max-width: 1200px) {
  h1,
  .h1 {
    font-size: calc(1.375rem + 1.5vw);
  }
}
h2,
.h2 {
  font-size: 2rem;
}
@media (max-width: 1200px) {
  h2,
  .h2 {
    font-size: calc(1.325rem + 0.9vw);
  }
}

h3,
.h3 {
  font-size: 1.75rem;
}

@media (max-width: 1200px) {
  h3,
  .h3 {
    font-size: calc(1.3rem + 0.6vw);
  }
}
h4,
.h4 {
  font-size: 1.5rem;
}

@media (max-width: 1200px) {
  h4,
  .h4 {
    font-size: calc(1.275rem + 0.3vw);
  }
}
h5,
.h5 {
  font-size: 1.25rem;
}

h6,
.h6 {
  font-size: 1rem;
}

.underline_anim {
  padding-bottom: 6px;
  width: max-content;
  background-image: linear-gradient(to right, currentColor 0%, currentColor 100%);
  background-position: 0 100%;
  background-repeat: no-repeat;
  -webkit-background-size: 0 1px;
  background-size: 0 1px;
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}
.underline_anim.show_underline_on_hover:hover {
  animation: underline_hover_on 0.8s cubic-bezier(0.25, 0.8, 0.25, 1) both;
}
.underline_anim.hide_underline_on_hover {
  animation: underline_hover_on 0.8s cubic-bezier(0.25, 0.8, 0.25, 1) both;
}
.underline_anim.hide_underline_on_hover:hover {
  animation-name: underline_hover_off;
}

@keyframes underline_hover_on {
  0% {
    background-size: 0 1px;
  }
  100% {
    background-size: 100% 1px;
  }
}
@keyframes underline_hover_off {
  0% {
    background-size: 100% 1px;
  }
  100% {
    background-size: 0 1px;
  }
}
.header {
  padding: 1.5rem;
}
.header .header_container {
  height: 74px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .header_container .header_logo img {
  max-width: 256px;
}
.header .header_container .header_menu {
  list-style: none;
  margin: 0;
  padding: 0;
}
.header .header_container .header_menu .menu_item, .header .header_container .header_menu > li {
  display: inline-block;
  padding: 0.75rem;
}
.header .header_container .header_menu .menu_item a, .header .header_container .header_menu > li a {
  transition: color 0.3s ease;
}
.header .header_container .header_menu .menu_item a:hover, .header .header_container .header_menu > li a:hover {
  color: #ea011d;
  text-decoration: none;
}
.header .header_container .header_tools_search {
  cursor: pointer;
}

.latest-posts_subtitle {
  font-size: 0.875rem;
  text-transform: uppercase;
  margin-bottom: 0.5rem;
  text-align: center;
  font-weight: 500;
}
.latest-posts_title {
  font-size: 3.5rem;
  margin-bottom: 1.5rem;
  text-align: center;
  font-weight: 900;
}
.latest-posts_grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, 295px);
  gap: 30px;
  justify-content: center;
}
.latest-posts_grid_item_image {
  border-radius: 1rem;
  overflow: hidden;
  display: block;
}
.latest-posts_grid_item_image img {
  aspect-ratio: 1/1;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}
.latest-posts_grid_item_content {
  margin-top: 1.5rem;
  padding-left: 50px;
  position: relative;
}
.latest-posts_grid_item_content::before {
  content: "";
  position: absolute;
  left: 0;
  top: 7px;
  display: inline-block;
  width: 35px;
  height: 2px;
  background-color: #020202;
}
.latest-posts_grid_item_meta {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  margin-bottom: 0.75rem;
}
.latest-posts_grid_item_meta_category {
  text-transform: uppercase;
  font-size: 13px;
  transition: color 0.3s ease;
}
.latest-posts_grid_item_meta_category:hover {
  color: #ea011d;
}
.latest-posts_grid_item_meta_date {
  color: #8f929d;
  font-size: 14px;
}
.latest-posts_grid_item_meta_date::before {
  content: "•";
  display: inline-block;
  vertical-align: text-bottom;
  font-size: 14px;
  font-weight: 400;
  margin: 0 0.75em 0 0.45em;
}
.latest-posts_grid_item_title {
  font-size: 1.5rem;
  font-weight: 500;
}
.latest-posts_grid_item:hover .latest-posts_grid_item_image img {
  transform: scale(1.05);
}

.content .wp-block-cover {
  margin: 0 1.25rem 2rem;
  min-height: 70vh;
  border-radius: 1rem;
  padding: 30px;
}
@media (min-width: 1024px) {
  .content .wp-block-cover {
    margin: 0 4rem 2rem;
    padding: 30px;
    justify-content: flex-start;
  }
}
@media (min-width: 1320px) {
  .content .wp-block-cover .wp-block-cover__inner-container {
    width: 50%;
  }
}
.content .wp-block-cover .wp-block-cover__inner-container .wp-block-heading {
  margin-bottom: 2rem;
  text-shadow: black 1px 0 10px;
}
@media (min-width: 1024px) {
  .content .wp-block-cover .wp-block-cover__inner-container .wp-block-heading {
    font-size: 3.5rem !important;
  }
}
.content .wp-block-cover .wp-block-cover__inner-container > p {
  text-shadow: black 1px 0 10px;
}
.content .wp-block-cover .wp-block-cover__inner-container .wp-block-buttons {
  margin-top: 1.5rem;
}

.wp-block-buttons .wp-element-button {
  padding: 17px 45px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  vertical-align: top;
  box-sizing: border-box;
  border-radius: 30px;
  letter-spacing: 0;
  white-space: nowrap;
  height: auto;
  max-width: 100%;
  color: #fcfcfc;
  background-color: #ea011d;
  border-color: #ea011d;
  border-width: 0;
  box-shadow: 0 7px 25px rgba(234, 1, 29, 0.2);
  transition: all 0.3s ease;
}
.wp-block-buttons .wp-element-button:hover {
  border-color: #cb0018;
  background-color: #cb0018;
}

.footer {
  background-color: #020202;
  color: #fcfcfc;
  padding: 100px 0 20px;
}
.footer .footer_container .footer_nav {
  margin-bottom: 4rem;
  display: flex;
  justify-content: space-between;
  gap: 1.5rem;
}
.footer .footer_container .footer_nav_title {
  font-size: 1.25rem;
  margin-bottom: 1.75rem;
  font-weight: 800;
}
.footer .footer_container .footer_nav_logo img {
  width: 250px;
  filter: brightness(0) invert(1);
}
.footer .footer_container .footer_nav_logo_description {
  margin-top: 2rem;
  max-width: 300px;
}
.footer .footer_container .footer_nav_info_address {
  margin-bottom: 1.5rem;
  line-height: 1.8;
}
.footer .footer_container .footer_nav_info_mail {
  display: inline-block;
  font-size: 1.125rem;
  text-decoration: none;
  position: relative;
  margin-bottom: 1.5rem;
}
.footer .footer_container .footer_nav_info_tel {
  font-size: 1.25rem;
  font-weight: 700;
}
.footer .footer_container .footer_nav_links_menu {
  list-style: none;
  margin: 0;
  padding: 0;
}
.footer .footer_container .footer_nav_links_menu .menu-item {
  margin-bottom: 1rem;
}
.footer .footer_container .footer_nav_social_box {
  display: flex;
  gap: 0.5rem;
}
.footer .footer_container .footer_nav_social_box_icon {
  width: 45px;
  height: 45px;
  border: 1px solid rgba(252, 252, 252, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 45px;
  transition: color 0.3s ease;
}
.footer .footer_container .footer_nav_social_box_icon.hover-facebook:hover {
  color: #3b5998;
}
.footer .footer_container .footer_nav_social_box_icon.hover-twitter:hover {
  color: #1d9ee8;
}
.footer .footer_container .footer_nav_social_box_icon.hover-instagram:hover {
  color: #e23756;
}
.footer .footer_container .footer_copyright {
  border-top: 1px solid #3c3f47;
  padding: 2rem 0 0.5rem;
}

html,
body {
  font-family: "Noto Sans", sans-serif;
  font-size: 16px;
  color: #1c1919;
}

.container {
  padding: 0 5rem;
}

.wrapper,
.content > p,
.content > .wp-block-heading {
  width: 100%;
  padding: 0 1rem;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 1320px) {
  .wrapper,
  .content > p,
  .content > .wp-block-heading {
    max-width: 1320px;
  }
}