@use "variables" as *;
.latest-posts {
  &_subtitle {
    font-size: 0.875rem;
    text-transform: uppercase;
    margin-bottom: 0.5rem;
    text-align: center;
    font-weight: 500;
  }
  &_title {
    font-size: 3.5rem;
    margin-bottom: 1.5rem;
    text-align: center;
    font-weight: 900;
  }
  &_grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, 295px);
    gap: 30px;
    justify-content: center;

    &_item {
      &_image {
        border-radius: 1rem;
        overflow: hidden;
        display: block;
        img {
          aspect-ratio: 1 / 1;
          height: 100%;
          object-fit: cover;
          transition: transform 0.5s ease;
        }
      }
      &_content {
        margin-top: 1.5rem;
        padding-left: 50px;
        position: relative;
        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 7px;
          display: inline-block;
          width: 35px;
          height: 2px;
          background-color: $color-black;
        }
      }
      &_meta {
        display: flex;
        align-items: center;
        gap: 0.3rem;
        margin-bottom: 0.75rem;

        &_category {
          text-transform: uppercase;
          font-size: 13px;
          transition: color 0.3s ease;
          &:hover {
            color: $color-red;
          }
        }

        &_date {
          color: $color-gray-light;
          font-size: 14px;

          &::before {
            content: "•";
            display: inline-block;
            vertical-align: text-bottom;
            font-size: 14px;
            font-weight: 400;
            margin: 0 0.75em 0 0.45em;
          }
        }
      }
      &_title {
        font-size: 1.5rem;
        font-weight: 500;
      }
      &:hover {
        .latest-posts_grid_item_image {
          img {
            transform: scale(1.05);
          }
        }
      }
    }
  }
}
